<template>
    <div class="order">
        <Loading :active.sync="isLoading"></Loading>
        <table class="table mt-4">
        <thead>
            <tr>
              <th></th>
              <th>Email</th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
        </thead>
        <tbody>
          <!--  -->
            <tr v-for="(item, key) in sortOrder" :key="key"
            v-if="orders.length"
            :class="{'text-secondary': !item.is_paid}">
            <td>{{ item.create_at | dateFilters }}</td>
            <td><span v-text="item.user.email" v-if="item.user"></span></td>
            <td>
                <ul class="list-unstyled">
                <li v-for="(product, i) in item.products" :key="i">
                    {{ product.product.title }} {{ product.qty }}
                    {{ product.product.unit }}
                </li>
                </ul>
            </td>
            <td class="text-right">{{ item.total | currencyFilters }}</td>
            <td>
                <strong v-if="item.is_paid" class="text-success"></strong>
                <span v-else class="text-muted"></span>
            </td>
            </tr>
        </tbody>
        </table>
        <!-- props  -->
        <pagination :pages="pagination" @emitPages="getOrders"></pagination>
    </div>
</template>

<script>
import pagination from './pagination'

export default {
  data() {
    return {
      //
      orders: {},
      isNew: false,
      pagination: {},
      isLoading: false,
    };
  },
  components: {
    pagination
  },
  methods: {
    // 
    getOrders(currentPage = 1) {
      const vm = this;
      // API 
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/orders?page=${currentPage}`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        // 
        vm.orders = response.data.orders;
        // 
        vm.pagination = response.data.pagination;
        vm.isLoading = false;
        console.log(vm.pagination)
      });
    },
  },
  computed: {
    // 
    sortOrder() {
      
      const vm = this;
      let newOrder = [];
      
      if (vm.orders.length) {
        newOrder = vm.orders.sort((a, b) => {
          const aIsPaid = a.is_paid ? 1 : 0;
          const bIsPaid = b.is_paid ? 1 : 0;
          return bIsPaid - aIsPaid;
        });
      }
      // 
      return newOrder;
    },
  },
  created() {
    this.getOrders();
  },
};
</script>

<style lang="scss" scoped>
.order{
  margin-top: 30px;
}
</style>




