// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import axios from 'axios'
import VueAxios from 'vue-axios'
import Loading from 'vue-loading-overlay'
import 'vue-loading-overlay/dist/vue-loading.min.css'
import 'bootstrap'
import './bus'
import currencyFilters from './filters/currency'
import dateFilters from './filters/date'
import VeeValidate from 'vee-validate';
// 
import zhTWValidate from "vee-validate/dist/locale/zh_TW";


// 
import VCharts from 'v-charts'
import histogram from 'v-charts/lib/histogram.common'

import Vue from 'vue'
import App from './App'
import router from './router'

Vue.config.productionTip = false
// Vue.use() 
Vue.use(VueAxios, axios)
Vue.use(VeeValidate);
Vue.use(VCharts)
// 
VeeValidate.Validator.localize('zh_TW', zhTWValidate)

// Vue.component
Vue.component('Loading', Loading);

Vue.component('histogram', histogram)


// Vue 
// 
Vue.filter('currencyFilters', currencyFilters);
Vue.filter('dateFilters', dateFilters);

/* 
to: 
from: 
next: 
beforeEach
*/
router.beforeEach((to, from, next) => {
  // meta requiresAuth if
  if (to.meta.requiresAuth) {
    // API 
    const api = `${process.env.APIPATH}/api/user/check`;
    // ajax"axios"router.beforeEach()vuethisvue
    axios.post(api).then((response) => {
      console.log('' + response.data.success);
      // 
      if (response.data.success) {
        console.log(response.data);
        next();
      } else {
        //  path:'/login'
        next({
          path: '/login'
        });
        console.log(response.data.message);
      }
    })
  } else {
    next();
  }

})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  render: h => h(App),
  router,
  components: { App },
  template: '<App/>'
})





