<template>
  <div>
    {{ msg }}
    <a href="#" @click.prevent="backTologin"></a>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: ''
    }
  },
  methods:{
    backTologin(){
        this.$router.push('/login');
    }
  }
}
</script>

