<template>
  <div class="products">
    <div>
        <loading :active.sync="isLoading"></loading>
    </div>
    <div class="text-right">
      <button class="btn btn-secondary" @click="openModal(true)"></button>
    </div>
      <table class="table mt-4">
        <thead>
          <tr>
            <th width='120'></th>
            <th></th>
            <th width='80'></th>
            <th width='80'></th>
            <th width='120'></th>
            <th width='120'></th>
            <th width='120'></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item) in products" :key="item.id">
            <td>{{ item.category }}</td>
            <td>{{ item.title }}</td>
            <!--  -->
            <td class="text-right">{{ item.origin_price | currencyFilters }}</td>
            <td class="text-right">{{ item.price | currencyFilters }}</td>
            <td>
              <span v-if="item.is_enabled"></span>
              <span v-else></span>
            </td>
            <td>
              <button class="btn btn-primary" @click="openModal(false, item)"></button>
            </td>
            <td>
              <button class="btn btn-danger"  @click="delModal(item)"></button>
            </td>
          </tr>
        </tbody>
      </table>
      
      <!--    @emitPages="" -->
      <pagination :pages="pagination" @emitPages="getProducts"></pagination>

      <!-- Modal id-->
      <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog modal-lg" role="document">
          <div class="modal-content border-0">
            <div class="modal-header bg-dark text-white">
              <h5 class="modal-title" id="exampleModalLabel">
                <span>{{ isNewText }}</span>
              </h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <div class="row">
                <div class="col-sm-4">
                  <div class="form-group">
                    <label for="image"></label>
                    <input type="text" class="form-control" id="image"
                      v-model="tempProduct.imageUrl"
                      placeholder="" >
                  </div>
                  <div class="form-group">
                    <label for="customFile"> 
                      <!-- fontawesomehtml -->
                      <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                    </label>
                    <!-- input change uploadFile function -->
                    <input type="file" id="customFile" class="form-control"
                      ref="files" @change="uploadFile">
                  </div>
                  <img class="img-fluid" :src="tempProduct.imageUrl" alt="">
                </div>
                <div class="col-sm-8">
                  <div class="form-group">
                    <label for="title"></label>
                    <input type="text" class="form-control" id="title"
                      v-model="tempProduct.title"
                      placeholder="">
                  </div>

                  <div class="form-row">
                    <div class="form-group col-md-6">
                      <label for="category"></label>
                      <input type="text" class="form-control" id="category"
                        v-model="tempProduct.category"
                        placeholder="">
                    </div>
                    <div class="form-group col-md-6">
                      <label for="price"></label>
                      <input type="unit" class="form-control" id="unit"
                        v-model="tempProduct.unit"
                        placeholder="">
                    </div>
                  </div>

                  <div class="form-row">
                    <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                      <input type="number" class="form-control" id="origin_price"
                        v-model="tempProduct.origin_price"
                        placeholder="">
                    </div>
                    <div class="form-group col-md-6">
                      <label for="price"></label>
                      <input type="number" class="form-control" id="price"
                        v-model="tempProduct.price"
                        placeholder="">
                    </div>
                  </div>
                  <hr>

                  <div class="form-group">
                    <label for="description"></label>
                    <textarea type="text" class="form-control" id="description"
                      v-model="tempProduct.description"
                      placeholder=""></textarea>
                  </div>
                  <div class="form-group">
                    <label for="content"></label>
                    <textarea type="text" class="form-control" id="content"
                      v-model="tempProduct.content"
                      placeholder=""></textarea>
                  </div>
                  <div class="form-group">
                    <div class="form-check">
                      
                      <input class="form-check-input" type="checkbox"
                        v-model="tempProduct.is_enabled"
                        :true-value="1"
                        :false-value="0"
                        id="is_enabled">
                      <label class="form-check-label" for="is_enabled">
                        
                      </label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
              <button type="button" class="btn btn-primary" @click="updateProduct"></button>
            </div>
          </div>
        </div>
      </div>
      <!--  -->
      <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             <strong class="text-danger">{{ tempProduct.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger"
              @click="delProduct" ></button>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import $ from 'jquery';
import pagination from './pagination';

export default {
  data () {
    return {
      // 
      products:[],
      //  item
      tempProduct:{},
      //
      isNew:false,
      isNewText:'',
      // Loading
      isLoading:false,
      status:{
        fileUploading:false,
      },
      //
      pagination:{}
    };
  },
  // 
  components:{
    pagination
  },
  methods:{
    //
    // ES6  API 
    getProducts(page = 1){
      const vm = this;
      // API  API
      const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/admin/products?page=${page}`;
      // getProductsisLoading
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        //  products products
        vm.products = response.data.products;
        // APIisLoading
        vm.isLoading = false;
        console.log(vm.products);
        // APIpagination  pagination 
        vm.pagination = response.data.pagination;
        console.log(vm.pagination);
      });
    },
    //  bootstrap modal 
    // item 
    openModal(isNew, item){
      // 
      if(isNew){
        //  tempProduct 
        this.tempProduct = {};
        this.isNew = true;
        this.isNewText = ''
      }else{
        // 
        // itemitem  tempProduct
        this.tempProduct =  Object.assign({}, item);
        this.isNew = false;
        this.isNewText = ''
      }
      $('#productModal').modal('show');
    },
    //
    updateProduct(){
      const vm = this;
      // API
      let api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/admin/product`;
      let httpMethod = "post";
      // vm.isNewvm.isNew = trueapiapi
      if(!vm.isNew){
        // API
        api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/admin/product/${vm.tempProduct.id}`;
        // API
        httpMethod = "put";
      }
      // api
      // API
      this.$http[httpMethod](api, { data: vm.tempProduct })
      .then((response) => {
        // 
        if(response.data.success){
          // 
          vm.getProducts();
          // /
          $('#productModal').modal('hide');
        }else{
          vm.getProducts();
          $('#productModal').modal('hide');
          console.log(response);
        }
      });
    },
    //  Modal
    // item
    delModal(item){
      $('#delProductModal').modal('show');
      // item  tempProduct
      this.tempProduct =  Object.assign({}, item);
    },
    //
    delProduct(){
      const vm = this;
      //api id
      let api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/admin/product/${vm.tempProduct.id}`;
      // API delete
      this.$http.delete(api)
      .then((response) => {
        console.log(response);
        //
        this.getProducts();
        // modal
        $('#delProductModal').modal('hide');
      });
    },
    // 
    uploadFile(){
      //   component
      const uploadedFile = this.$refs.files.files[0];
      const vm = this ;
      vm.status.fileUploading = true;
      // FormDataform
      const formData = new FormData();
      // formDataformData.append(name, value);
      // name: ,value:
      formData.append('file-to-upload', uploadedFile);

      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
      // APIpostAPI
      // urlAPI, formDataAPI, {}form-data
      this.$http.post(url, formData, {
        // headers
        headers: {
           'Content-Type': 'multipart/form-data',
         },
      }).then((response) => {
        //  Vue.set( target, key, value ) API 
        // target:, key:() , value:
        
        if(response.data.success){
          vm.$set(vm.tempProduct,'imageUrl',response.data.imageUrl)
          vm.status.fileUploading = false;
          console.log(vm)
        }else{
          console.log(vm)
          // vue
          vm.$bus.$emit('messsage:push', response.data.message, 'danger');
        }
      })
      //  > formData > API
    }
  },
  created() {
    this.getProducts();
  },
}
</script>

<style lang="scss" scoped>
.products{
  margin-top:30px;
}
</style>



