<template>
    <div>
        <!-- pagination -->
        <nav aria-label="Page navigation example">
            <ul class="pagination">
            <!-- false -->
            <li class="page-item" :class="{'disabled': !pages.has_pre }">
                <!--   page -->
                <a class="page-link" href="#" aria-label="Previous" @click="updatePage(pages.current_page-1)">
                <span aria-hidden="true">&laquo;</span>
                <span class="sr-only">Previous</span>
                </a>
            </li>
            <!-- APIpaginationpagination -->
            <!--  -->
                <li class="page-item" v-for="page in pages.total_pages" :key="page"
                :class="{ 'active': pages.current_page === page}">
                <!--  getProducts page -->
                <a class="page-link" href="#" @click.prevent="updatePage(page)">{{ page }}</a>
            </li>
            <li class="page-item" :class="{'disabled': !pages.has_next}">
                <!--   page -->
                <a class="page-link" href="#" aria-label="Next" @click="updatePage(pages.current_page+1)">
                <span aria-hidden="true">&raquo;</span>
                <span class="sr-only">Next</span>
                </a>
            </li>
            </ul>
        </nav>
    </div>
</template>

<script>
// :pages="{  }"
// @emitPages=""
export default {
    name: 'Layout',
    //props
    props: ['pages'],
    methods: {
        updatePage(page) {
            // 
            this.$emit('emitPages', page);
        },
    },
};
</script>
<!--


>  

> pagination

> paginationprop 

> paginationthis.$emit (this.$emit(eventName,args))))

> emitPages,

-->



