<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <Header></Header>
        <!--  -->
        <div class="content">
            <div class="left">
            <!-- submit  -->
                <form class="order-form" @submit.prevent="createOrder">
                    <div class="form-group">
                        <label for="useremail">Email</label>
                        <input type="email" class="form-control" name="email" id="useremail"
                            v-model="form.user.email" 
                            placeholder=" Email" 
                            :class="{ 'is-invalid': errors.has('email') }"
                            v-validate="'required'">
                        <span class="text-danger">{{ errors.first('email') }}</span>
                    </div>
                
                    <div class="form-group">
                        <label for="username"></label>
                        <input type="text" class="form-control" name="name" id="username"
                            placeholder="" 
                            :class="{ 'is-invalid': errors.has('name') }"
                            v-model="form.user.name" 
                            v-validate="'required'">
                        <span class="text-danger" v-if="errors.has('name')"></span>
                    </div>
                
                    <div class="form-group">
                        <label for="usertel"></label>
                        <input type="tel" 
                            class="form-control" 
                            id="usertel" 
                            name="usertel"
                            v-model="form.user.tel" 
                            placeholder=""
                            v-validate="'required'"
                            :class="{ 'is-invalid': errors.has('usertel') }">
                        <span class="text-danger" v-if="errors.has('usertel')"></span>
                    </div>
                
                    <div class="form-group">
                        <label for="useraddress"></label>
                        <input type="address" 
                            class="form-control" 
                            name="address" 
                            id="useraddress" 
                            v-model="form.user.address"
                            :class="{ 'is-invalid': errors.has('address') }"
                            v-validate="'required'"
                            placeholder="">
                        <span class="text-danger" v-if="errors.has('address')"></span>
                    </div>
                
                    <div class="form-group">
                        <label for="useraddress"></label>
                        <textarea name="" id="" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
                    </div>
                    <div class="text-right">
                        <button class="btn"></button>
                    </div>
                </form>
            </div>

            <div class="right">
                <div class="order-item">
                    <div class="pay-detail">
                        <div class="pay-info">
                            <span></span>
                            <div class="payTotal">
                                <span>NT$ </span>
                                <span>{{cart.total | currencyFilters}}</span>
                            </div>
                        </div>
                        <div class="pay-discount" v-if="cart.final_total !== cart.total">
                            <span></span>
                            <div class="discountTotal">
                                <span>NT$ </span>
                                <span>{{ cart.final_total | currencyFilters }}</span>
                            </div>
                        </div>
                        <div class="pay-discount">
                            <span></span>
                            <div class="discountTotal">
                                <span>NT$ </span>
                                <span>{{ cart.final_total | currencyFilters }}</span>
                            </div>
                        </div>
                    </div>

                    <div class="backToCart">
                        <router-link to="/shopping_cart" class="backToCartBtn"></router-link>
                    </div>

                </div>
            </div>
        </div>
        <Footer></Footer>
    </div>
</template>

<script>
import $ from 'jquery';
import Header from '../index_page/header';
import Footer from '../index_page/footer';

export default {
    data(){
        return{
            // 
            products:[],
            // 
            product:[],
            // 
            isLoading:false,
            // 
            status:{
                LoadingItem:" ",
            },
            //  
            cart:[],
            // 
            coupon_code:'',
            // 
            form:{
                user:{
                name:" ",
                email:" ",
                tel: " ",
                address: " ",
                },
                message:" ",
            }
        }
    },
    components:{
        Header,
        Footer,
    },
    methods:{
        getCart(){
            const vm = this;
            const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/cart`;
            // GET
            this.$http.get(api).then((response)=>{
                //  cart 
                vm.cart = response.data.data;
                console.log(vm.cart)
            })
        },
        //
        addCouponCartItem(){
            const vm = this;
            // API
            const coupon = {
                code: vm.coupon_code,
            }
            const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/coupon`;
            // post coupon_codeAPI
            this.$http.post(api, { data: coupon}).then((response)=>{
                console.log(response)
                this.getCart();
            })
        },
        //
        createOrder(){
            const vm = this;
            const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/order`;
            this.isLoading = true;
            // validate  
            this.$validator.validate().then( (result) => {
                if (result) {
                // post vm.formAPI  API 
                this.$http.post(api, { data: vm.form }).then((response)=>{
                    if(response.data.success){
                        // post   
                        vm.$router.push(`/paycheckorder/${response.data.orderId}`)
                    }
                    console.log(response)
                })
                } else {
                    console.log('')
                }
            });
        }
    },
    created(){
        this.getCart();
    }
}
</script>

<style lang="scss" scoped>
.content{
    display: flex;
    >.left{
        width: 60%;
        >.order-form{
            width: 60%;
            margin-left: auto;
            >.text-right{
                >.btn{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 100%;
                    height: 50px;
                    padding: 10px;
                    margin-bottom: 30px;
                    border-radius: 5px;
                    background: #f5548c;
                    font-size: 18px;
                    color: #ffffff;
                    letter-spacing: 3px;
                    transition: all 0.3s;
                    &:hover{
                        text-decoration: none;
                        transform: translate3d(0,-5px,0);
                        box-shadow: 0 0.2em 0.2em -0.1em #c83166, 0 0.3em #a32252, 0 0.5em 0.5em -0.1em rgba(0,0,0,0.12);
                    }
                }
            }
        }
    }
    >.right{
        width: 40%;
        >.order-item{
            width: 40%;
            border: 1px solid #ddd;
            border-radius: 5px;
            margin-left: 10%;
            margin-top: 28px;
            >.pay-detail{
                margin: 15px 0;
                >div{
                    height: 40px;
                    display: flex;
                    align-items: center;
                    margin: 0 10px;
                    >div{
                        margin-left: auto;
                    }
                }
            }
            >.backToCart{
                margin:0 10px;
                padding: 20px 0;
                border-top: 1px solid #ddd;
                >.backToCartBtn{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 100%;
                    height: 50px;
                    padding: 10px;
                    border-radius: 5px;
                    background: #f5548c;
                    font-size: 18px;
                    color: #ffffff;
                    letter-spacing: 3px;
                    transition: all 0.3s;
                    &:hover{
                        text-decoration: none;
                        transform: translate3d(0,-5px,0);
                        box-shadow: 0 0.2em 0.2em -0.1em #c83166, 0 0.3em #a32252, 0 0.5em 0.5em -0.1em rgba(0,0,0,0.12);
                    }
                }
            }
        }
    }
}
</style>



