<template>
    <!-- signout -->
    <div class="navbar">
        <div class="menuBtn">
            <!--  -->
            <i class="fas fa-bars fa-2x"></i>
            <div class="btnList">
                <ul>
                    <li>
                        <router-link to="/admin/cartdata"></router-link>
                    </li>
                    <li>
                        <router-link to="/admin/products"></router-link>
                    </li>
                    <li>
                        <router-link to="/admin/order"></router-link>
                    </li>
                    <li>
                        <router-link to="/admin/coupons"></router-link>
                    </li>
                    <li>
                        <router-link to="/"></router-link>
                    </li>
                </ul>
            </div>
        </div>
        <div class="navbarTitle"></div>
        <div class="signOut" @click="signout">
            <div class="signOutBtn"></div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            
        }
    },
    methods:{
        signout(){
            const api = `${ process.env.APIPATH }/logout`;
            const vm = this; //  Login vue component
            this.$http.post(api).then((response) => {
            
                if(response.data.success){
                    // vue component $router path
                    vm.$router.push('/login');
                }
            })
        }
    }
}
    
</script>

<style lang="scss" scoped>
.navbar{
    width: 100%;
    height: 50px;
    background-color: #343a40;
    position: relative;
    >.menuBtn{
        cursor: pointer;
        > i {
            color: #ffffff;
        }
        &:hover{
            >.btnList{
                display: block;
            }
        }
        >.btnList{
            position: absolute;
            top: 40px;
            border: 1px solid #ddd;
            background-color: #ffffff;
            width: 100px;
            z-index: 99;
            display: none;
            >ul{
                margin: 0;
                padding: 0;
                >li{
                    list-style: none;
                    padding: 15px;
                    >a{
                        font-size: 16px;
                        &:hover{
                            text-decoration: none;
                        }
                    }
                    &:hover{
                        background-color: #ddd;
                    }
                }
            }
        }
    }
    
    >.navbarTitle{
        color: #ffffff;
        font-size: 24px;
    } 
    >.signOut{
        border: 1px solid #ffffff;
        border-radius: 5px;
        padding: 10px;
        color: #ffffff;
        &:hover{
            cursor: pointer;
            background-color: #ffffff;
            color: #000000;
        }
    }
}
</style>



