<template>
    <div>
        <Header></Header>
        <div class="my-5 row justify-content-center">
            <!--  -->
            <form class="col-md-6" @submit.prevent="payOrder">
                <!--  -->
                <table class="table">
                <thead>
                    <th></th>
                    <th></th>
                    <th></th>
                </thead>
                <tbody>
                    <tr v-for="item in order.products" :key="item.id">
                    <td class="align-middle">{{ item.product.title }}</td>
                    <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                    <td class="align-middle text-right">{{ item.final_total | currencyFilters }}</td>
                    </tr>
                </tbody>
                <tfoot>
                    <tr>
                    <td colspan="2" class="text-right"></td>
                    <td class="text-right">{{ order.total | currencyFilters }}</td>
                    </tr>
                </tfoot>
                </table>
                <!--  -->
                <table class="table">
                <tbody>
                    <tr>
                    <th width="100">Email</th>
                    <td>{{ order.user.email }}</td>
                    </tr>
                    <tr>
                    <th></th>
                    <td>{{ order.user.name }}</td>
                    </tr>
                    <tr>
                    <th></th>
                    <td>{{ order.user.tel }}</td>
                    </tr>
                    <tr>
                    <th></th>
                    <td>{{ order.user.address }}</td>
                    </tr>
                    <tr>
                    <th></th>
                    <td>
                        <span v-if="!order.is_paid"></span>
                        <span v-else class="text-success"></span>
                    </td>
                    </tr>
                </tbody>
                </table>
                <div class="text-right" v-if="order.is_paid === false">
                    <button class="btn btn-danger"></button>
                </div>
                <div class="text-right" v-if="order.is_paid === true">
                    <router-link to="/" class="btn btn-danger"></router-link>
                </div>
            </form>
        </div>
        <Footer></Footer>
    </div>
</template>

<script>
import Header from '../index_page/header'
import Footer from '../index_page/footer'

export default {
    components:{
        Header,
        Footer
    },
    data(){
        return{
            orderId:'',
            order: {
                user:{}
            }
        }
    },
    //  ID >  APIID > 
    methods:{
        // 
        getOreder(){
            const vm = this;
            const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/order/${vm.orderId}`;
            this.$http.get(api).then((response) => {
                vm.order = response.data.order;
                console.log(response.data.order);
            });
        },
        // 
        payOrder(){
            const vm = this;
            const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/pay/${vm.orderId}`;
            this.$http.post(api).then((response) => {
                console.log(response.data.order);
                if(response.data.success){
                    this.getOreder();
                    console.log(response.data);
                }
            });
        }
    },
    created(){
        //  params
        this.orderId = this.$route.params.orderId;
        console.log(this.orderId);
        this.getOreder();
    }
}
</script>



